export default class AppStart extends Component
{
  state = {
    modalVisible: true
  }
  constructor(props){
    super(props)
    this.onDimensionChange = this.onDimensionChange.bind(this)
    this.setProperDimensions({window: Dimensions.get("window")})
  }

  setProperDimensions(dims){
    console.log("changed")
    console.log(dims)
    styles.container.height = dims.window.height
    styles.container.width = dims.window.width
    if (dims.window.width > dims.window.height){
      // In case landscape, change width and add border and shadows
      console.log("landscape")
      var newstyle = {
        width : dims.window.height * 1.2,
        height: Dimensions.get("window").height,
        borderColor : "#f16522",
        borderWidth : 1,
        borderRadius : 20,
        backgroundColor: "#FFF",

        // SHADOW
        shadowColor: "#000",
        shadowOffset: {
        	width: 0,
        	height: 12,
        },
        shadowOpacity: 0.58,
        shadowRadius: 16.00,

        elevation: 24,
      }
      styles.container = newstyle
      var newmodalinner = {
        backgroundColor: "#FFF",
        borderWidth: 3,
        borderColor: '#f16522',
        width : newstyle.width * 0.9,
        height: Dimensions.get("window").height * 0.8,
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: 20,
        justifyContent: 'space-around',

      }
      styles.modalinner = newmodalinner
    }
  }

  onDimensionChange(dims){
    this.setProperDimensions(dims)
    console.log(dims.window.width)
    console.log(dims.window.height)
    this.forceUpdate()
  }

  componentWillMount() {
    Dimensions.addEventListener("change", this.onDimensionChange);
  }

  componentWillUnmount() {
    // Important to stop updating state after unmount
    Dimensions.removeEventListener("change", this.onDimensionChange);
  }

  welcometext = "Vitaj, \n "+
  "Milý kultúrne založený človek, si na správnom mieste. \n\n" +
  "Hangouter je služba, ktorá ti ponúka zoznam podujatí v okolí Prešova. \n"+
  "Funguje to ako Google na podujatia \n" +
  "...TextTextText... \n\n"+
  "Sme tím nadšencov, ktorí sa snažia propagovať kultúru v Prešove \n" +
  "Všetko je po prvý raz, rovnako ako náš web. \n" +
  "Hoci sme ho vytvorili s láskou a nadšením, \n" +
  "predsa len, nie všetko je dokonalé. \n\n"+
  "Ak narazíte na nejakú nedokonalosť, neváhajte nás kontaktovať na: support@hangouter.tk \n"+
  "Ďakujeme. :) "

  render(){
    console.log(styles.container)
    return(
      <View source={require("./assets/logores.png")} style={styles.outercontainer}
      resizeMode="repeat">
        <View style={styles.container}>
          <HashRouter style={styles.innerrouter}>
            <View style={styles.header}>
              <Image source={require("./assets/logores.png")} style={styles.headerImage}/>
              </View>

              <Modal
                animationType="slide"
                transparent={true}
                visible={this.state.modalVisible}>
                <View style={[styles.modalview, this.state.modalVisible ? {backgroundColor: 'rgba(0,0,0,0.5)'} : '']}>
                  <View style={styles.modalinner}>
                    <Text style={styles.welcometext}>{this.welcometext}</Text>
                      <TouchableHighlight
                      onPress={() => {
                        this.setState({modalVisible: false});
                      }}
                      style={styles.vstupte}>
                      <Text style={styles.vstuptext}>Vstúpte</Text>
                    </TouchableHighlight>
                  </View>
                </View>
              </Modal>

              <Switch>
                <Route exact path="/" render={props => <HomeScreen {...props}/>} />
                <Route path="/search" render={props => <SearchScreen {...props}/>} />
                <Route path="/savedevents" render={props => <SavedEventsScreen {...props}/>} />
                <Route path="/appinfo" render={props => <MoreScreen {...props}/>} />
                <Route path="/details/:id" render={props => <DetailsScreen {...props}/>} />
                <Route path="/searchresults/" render={props => <SearchResultsScreen {...props}/>} />
                <Route path="/admin" render={props => <AdminScreen {...props}/>} />
                <Route path="/welcome" render={props => <WelcomeScreen {...props}/>} />
                <Route render={props => <Screen404 {...props}/>} />
              </Switch>

              <View style={styles.bottomTabBar}>
                <HomeButton/>
                <SearchButton/>
                <MoreButton style={styles.button}/>
              </View>

              </HashRouter>
              </View>
      </View>
    )}
}

const btn = {
active: '#FF9A46',
inactive: '#AAA'
}

const MoreButton = withRouter(({ history, location}) => {
  var route = "/appinfo"
  return(<TouchableOpacity style={styles.button} onPress={() => {history.push(route)}}>
    <Icon name='md-more' type="ionicon" size={30} color={route == location.pathname ? btn.active : btn.inactive} />
  </TouchableOpacity>)
  }
)

