    // fuck this I hate this, why is it not here, fuckit
    // TODO because of that, site will not use cached data
    if (this.state.isFetching){
    return (
      <WaitingComponent/>
    )
    }else{
      var item = this.state.event
      return (
        <View style={styles.outercontainer}>
          <ScrollView>
            <View style={styles.innercontainer}>
              <DetailsComponent item={item}/>
            </View>
            <View>
              <Text style={styles.desccontainer}>{item.description}</Text>
            </View>
            <EventLinks links={item.links}/>
          </ScrollView>
        </View>)
    }
